  transform: translate(-50%, -50%);
}
.showcases__imgLink::before {
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: opacity 0.3s;
}
.showcases__imgLink::after {
  left: calc(50% - 25px);
  top: calc(50% - 25px);
  width: 50px;
  height: 50px;
  background: transparent url("../img/arrow-video.svg") no-repeat center center;
  background-size: contain;
  opacity: 0;
  transform: scale(0.2);
  transition: opacity 0.4s, transform 0.3s;
}
.showcases__imgLink:hover::before {
  opacity: 1;
  transition: opacity 0.1s, transform 0.4s;
}
.showcases__imgLink:hover::after {
  opacity: 1;
  transform: scale(1);
}
