html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.link-reset,
.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include mobile {
    padding: 0 var(--container-offset-mobile);
    max-width: var(--container-width-mobile);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.accent-color {
  color: var(--accent-color);
}

.swiper-btns {
  display: flex;
  justify-content: center;
  gap: 12px;
}

.swiper-button-prev,
.swiper-button-next {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  border: 2px solid var(--dark-color);
  border-radius: 50%;
  width: 44px;
  height: 44px;
  background-color: var(--dark-color);
  transition: background-color 0.3s;

  &::after {
    content: none;
  }

  &:hover {
    background-color: var(--light-color);

    .swiper-button-icon {
      stroke: var(--dark-color);
    }
  }
}

.swiper-button-icon {
  display: block;
  width: 8px;
  height: 15px;
  fill: none;
  stroke: #fbfbfb;
  stroke-width: 1.55439;
}

.swiper-button-prev {
  transform: rotate(180deg);
}

.burger {
  @include burger;
}
